.header {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

}

.title {

    font-style: normal;
    font-weight: 900;
    font-size: 46px;
    line-height: 120%;
    color: #219653;

}

.text {

    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 26px;
    color: #5B7262;


}
@media (min-width: 1024px) {
    .header {
        padding-right: 20px;
    }
}
@media (max-width: 906px) {
    .title {
        text-align: center;
    }
    .header {
        margin: 0;
    }
    .text {
        text-align: center;
        margin-bottom: 50px;
    }
}